import {request} from './request'
//Permission
//用户登录
export function login(data){
    return request({
        method:'POST',
        url:'/api/Auth/LogIn',
        data:data
    })
}
//获取部门列表
export function getAllDeptForms(){
    return request({
        method:'GET',
        url:'/api/Auth/GetAllDeptForms'
    })
}
//获得所有职位信息
export async function getAllAuthPosition(data){
    return await request({
        method:'POST',
        url:'/api/Auth/GetAllAuthPosition',
        data:data
    })
}
//获得单个指定职位信息
export async function getSingleAuthPosition(data){
    return await request({
        method:'GET',
        url:'/api/Auth/GetSingleAuthPosition',
        params:{input:data}
    })
}
//新建职位
export async function InsertPosition(data){
    return await request({
        method:'POST',
        url:'/api/Auth/InsertPosition',
        data:data
    })
}
//更新职位
export async function UpdatePosition(data){
    return await request({
        method:'POST',
        url:'/api/Auth/UpdatePosition',
        data:data
    })
}
//删除职位
export async function DeletePosition(data){
    return await request({
        method:'DELETE',
        url:'/api/Auth/DeletePosition',
        data:data
    })
}


